<template>
   <div>
      <!--  -->
      <el-breadcrumb separator=">">
         <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
         <el-breadcrumb-item></el-breadcrumb-item>
         <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
         <!--  -->
         <el-row gutter:20>
            <el-col :span="8">
               <el-input
                  placeholder=""
                  clearable
                  v-model="queryInfo.query"
                  @clear="getOrderList()"
               >
                  <el-button
                     slot="append"
                     icon="el-icon-search"
                     @click="getOrderList()"
                  ></el-button>
               </el-input>
            </el-col>
         </el-row>

         <!--  -->
         <el-table border stripe :data="orderList">
            <el-table-column type="index" label="#"></el-table-column>
            <!--  -->
            <el-table-column
               label=""
               prop="order_number"
               min-width="200px"
            ></el-table-column>
            <!--  -->
            <el-table-column
               label=""
               prop="order_price"
            ></el-table-column>
            <el-table-column label="" prop="order_pay">
               <template slot-scope="scope">
                  <el-tag v-if="scope.row.order_pay === 0" type="danger"
                     ></el-tag
                  >
                  <el-tag v-else type="success"></el-tag>
               </template>
            </el-table-column>
            <el-table-column label="" prop="is_send">
               <template slot-scope="scope">
                  <el-tag v-if="scope.row.is_send === ''" type="danger"
                     ></el-tag
                  >
                  <el-tag v-else type="success"></el-tag>
               </template>
            </el-table-column>
            <el-table-column label="" prop="create_time">
               <template slot-scope="scope">
                  {{ scope.row.create_time | dataFormat }}
               </template>
            </el-table-column>
            <!--  -->
            <el-table-column label="">
               <template slot-scope="scope">
                  <!--  -->
                  <el-tooltip
                     effect="dark"
                     content=""
                     placement="top"
                     :enterable="false"
                  >
                     <el-button
                        type="primary"
                        size="mini"
                        icon="el-icon-edit"
                        @click="showChangeAddressDialog()"
                     ></el-button>
                  </el-tooltip>
                  <!--  -->
                  <el-tooltip
                     effect="dark"
                     content=""
                     placement="top"
                     :enterable="false"
                  >
                     <el-button
                        type="success"
                        size="mini"
                        icon="el-icon-s-opportunity"
                        @click="showProgress(scope.row.order_id)"
                     ></el-button> </el-tooltip
               ></template>
            </el-table-column>
         </el-table>

         <!--  -->
         <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[5, 7, 10, 20]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
         >
         </el-pagination>
      </el-card>

      <!--  -->
      <el-dialog
         :visible.sync="changeAddressDialogVisable"
         title=""
         @close="changeAddressDialogClosed()"
      >
         <el-form
            :model="addressForm"
            :rules="addressFormRulse"
            ref="addressFormRef"
         >
            <el-form-item label="/" prop="address1">
               <el-cascader
                  :options="cityData"
                  v-model="addressForm.address1"
               ></el-cascader>
            </el-form-item>
            <el-form-item label="" prop="address2">
               <el-input
                  v-model="addressForm.address2"
                  style="width: 70%"
               ></el-input>
            </el-form-item>
         </el-form>
         <!--  -->
         <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisable = false"> </el-button>
            <el-button type="primary" @click="ChangeAddress()"
               > 
            </el-button>
         </span>
      </el-dialog>

       <!--  -->
       <el-dialog
         :visible.sync="progressDialogVisalbe"
         title=""
         @close="ProgressDialogClosed()"
      >
        
         <!--  -->
         <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisable = false"> </el-button>
            <el-button type="primary" @click="ChangeAddress()"
               > 
            </el-button>
         </span>
      </el-dialog>
   </div>
</template>

<script>
import cityData from './citydata.js'

export default {
   data() {
      return {
         // 
         queryInfo: {
            query: '',
            pagenum: 1,
            pagesize: 5,
         },
         // 
         total: 0,
         // 
         orderList: [],
         //
         changeAddressDialogVisable: false,
         // 
         addressForm: {
            // 
            address1: [],
            // 
            address2: '',
         },
         // 
         progressDialogVisalbe:false,
         // 
         progress: [],
         // 
         addressFormRulse: {
            address1: [
               { required: true, message: '', trigger: 'blur' },
            ],
            address2: [
               { required: true, message: '', trigger: 'blur' },
            ],
         },
         //  
         cityData,
      }
   },
   created() {
      this.getOrderList()
   },
   methods: {
      // 
      async getOrderList() {
         const { data: res } = await this.$http.get(`orders`, {
            params: this.queryInfo,
         })
         if (res.meta.status !== 200) {
            console.log(res.meta.msg)
            this.$message.error('')
            return
         }
         this.orderList = res.data.goods
         this.total = res.data.total
      },
      // 
      showChangeAddressDialog() {
         this.changeAddressDialogVisable = true
      },
      // 
      ChangeAddress() {
         this.changeAddressDialogVisable = false
      },
      // 
      changeAddressDialogClosed(){
         this.$refs.addressFormRef.resetFields()
      },
      // pagesize
      handleSizeChange(newPagesize) {
         this.queryInfo.pagesize = newPagesize
         this.getOrderList()
      },
      // 
      handleCurrentChange(newPage) {
         this.queryInfo.pagenum = newPage
         this.getOrderList()
      },
      // 
      async showProgress(id) {
         this.$message.error('')
         // const testid="1106975712662"
         // const { data: res } = await this.$http.get(`/kuaidi/${id}`)
         // if (res.meta.status !== 200) {
         //    console.log(res.meta.msg)
         //    this.$message.error('')
         //    return
         // }
         
         // this.progress = res.data
         // console.log(this.progress)
         // this.progressDialogVisalbe = true
      },
      // 
      ProgressDialogClosed(){
         this.progress=[]
      },
   },
}
</script>

<style lang="less">
.el-cascader-panel {
   height: 350px !important;
}
</style>